// ===================
// TYPOGRAPHY
// ===================

#content_editor h1,
.pdf-page-content h1,
#content_editor h2,
.pdf-page-content h2,
#content_editor h3,
.pdf-page-content h3,
#content_editor h4,
.pdf-page-content h4,
#content_editor h5,
.pdf-page-content h5,
#content_editor h6,
.pdf-page-content h6,
#content_editor p,
.pdf-page-content p {
  line-height: 1.4 !important;
}

#content_editor h2,
.pdf-page-content h2 {
  @include mobile {
    font-size: 1.3em !important;
  }
  @include print {
    font-size: 1.5em !important; 
  }
}

#content_editor h4,
.pdf-page-content h4 {
  @include mobile {
    font-size: 1.15em !important;
  }
  @include print {
    font-size: 1.25em !important; 
  }
}

#content_editor p,
.pdf-page-content p {
  margin-bottom: 1em !important;
  @include print {
    font-size: 0.9em !important;
  }
}

#content_editor ul,
.pdf-page-content ul,
#content_editor ol,
.pdf-page-content ol {
  padding-left: 1em !important;
  margin-bottom: 1em !important;
  li {
    font-size: 0.9em;
    line-height: 1.4 !important;
    font-weight: 300;
  }
}

#content_editor hr,
.pdf-page-content hr {
  margin-top: 1em;
  margin-bottom: 1em;
  border: 0;
  border-top: 1px solid $brand-gray;
}

#content_editor a,
.pdf-page-content a {
  &:hover {
    color: lighten($brand, 15%);
  }
}

#content_editor table,
.pdf-page-content table {
  margin-bottom: 1em;
  border: 0;
  background-color: #fff;
  color: $base-text-color;
  
  td {
    padding: 0.5em;
    border-left: 1px solid $brand-gray;
    font-size: 0.9em !important;
    line-height: 1.4 !important;
    &:last-child {
      border-right: 1px solid $brand-gray;
    }
  }

  tr:nth-child(2n) td {
    background-color: #fff;
  }

  tr:last-child td {
    border-bottom: 1px solid $brand-gray;
  }

}

// GRID
#content_editor .no-gutters .half,
.pdf-page-content .no-gutters .half {
  padding: 0 30px !important;
  @include mobile {
    padding: 0 !important;
  }
  
  &:nth-child(2n-1) {
    padding-left: 0 !important;
  }
  
  &:nth-child(2n) {
    padding-right: 0 !important;
  }

}

#content_editor .no-gutters .one-third,
.pdf-page-content .no-gutters .one-third {
  padding: 0 30px !important;
  margin-bottom: 1.5em;
  @include mobile {
    padding: 0 !important;
    width: 100%;
  }
  @include print {
    padding: 0 0.125in !important;
    margin-bottom: 0.25in;
  }
  
  &:nth-child(3n-2) {
    padding-left: 0 !important;
    @include print {
      padding-left: 0in !important;
    }
  }
  
  &:nth-child(3n-1) {
    padding-left: 15px !important;
    padding-right: 15px !important;
    @include mobile {
      padding: 0 !important;
    }
    @include print {
      padding-left: 0.0625in !important;
      padding-right: 0.0625in !important;
    }
  }
  
  &:nth-child(3n) {
    padding-right: 0px !important;
    @include print {
      padding-right: 0in !important;
    }
  }

}

@supports (display: flex) {
  #content_editor .grid.no-gutters {
    display: flex;
    flex-wrap: wrap;
   .one-third {
    flex-basis: 33%;
    @include mobile {
      flex-basis: 100%;
    }
   }
  }
}